import { toURLSearchParams } from '@wix/sdk-runtime/rest-modules';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import { resolveUrl } from '@wix/sdk-runtime/rest-modules';
import { ResolveUrlOpts } from '@wix/sdk-runtime/rest-modules';
import { RequestOptionsFactory } from '@wix/sdk-types';

function resolveWixPaymentsDisputesV1DisputeServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {};

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_payments_disputes';

/** Get Dispute by ID */
export function getDispute(payload: object): RequestOptionsFactory<any> {
  function __getDispute({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.payments.disputes.v1.dispute',
      method: 'GET' as any,
      methodFqn: 'wix.payments.disputes.v1.DisputeService.GetDispute',
      packageName: PACKAGE_NAME,
      url: resolveWixPaymentsDisputesV1DisputeServiceUrl({
        protoPath: '/v1/disputes/{disputeId}',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'dispute.dueDate' },
              { path: 'dispute.createdDate' },
              { path: 'dispute.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __getDispute;
}

/** Retrieves a list of Disputes */
export function queryDisputes(payload: object): RequestOptionsFactory<any> {
  function __queryDisputes({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.payments.disputes.v1.dispute',
      method: 'POST' as any,
      methodFqn: 'wix.payments.disputes.v1.DisputeService.QueryDisputes',
      packageName: PACKAGE_NAME,
      url: resolveWixPaymentsDisputesV1DisputeServiceUrl({
        protoPath: '/v1/disputes/query',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'disputes.dueDate' },
              { path: 'disputes.createdDate' },
              { path: 'disputes.updatedDate' },
            ],
          },
        ]),
      fallback: [
        {
          method: 'POST' as any,
          url: resolveWixPaymentsDisputesV1DisputeServiceUrl({
            protoPath: '/v1/disputes/query',
            data: payload,
            host,
          }),
          data: payload,
        },
      ],
    };

    return metadata;
  }

  return __queryDisputes;
}

